<template>
<b-container fluid class="emsimg">
<v-btn fab dark color="#002b80" @click="onBack">
<v-icon color="white">mdi-arrow-left</v-icon>
</v-btn>
  <b-row>
    <b-col cols="12">
      <b-table striped hover :items="applyLeave" :fields="fields">
        <template slot="actions" scope="row">
          <b-btn size="sm" @click.stop="details(row.item)">Details</b-btn>
        </template>
      </b-table>
    </b-col>
  </b-row>
    </b-container>
</template>

<script>

import firebase from 'firebase'
import router from '../router'

export default {
  name: 'BoardList',
  data () {
    return {
      fields: {
        employeeId: { label: 'employeeId', sortable: true, class: 'text-left' },
        Name: { label: 'Name', sortable: true, class: 'text-left' },
        startDate: { label: 'startDate', sortable: true, class: 'text-left' },
        endDate: { label: 'endDate', sortable: true, class: 'text-left' }
        // actions: { label: 'Action', class: 'text-center' }
      },
      applyLeave: [],
      errors: [],
      ref: firebase.firestore().collection('applyLeave')
    }
  },
  created () {
    this.ref.onSnapshot((querySnapshot) => {
      this.applyLeave = []
      querySnapshot.forEach((doc) => {
        this.applyLeave.push({
        //   key: doc.id,
          employeeId: doc.data().eid,
          Name: doc.data().name,
          startDate: doc.data().sdate,
          endDate: doc.data().edate
        })
      })
    })
  },
  methods: {
    details (board) {
      router.push({ name: 'ShowBoard', params: { id: board.key } })
    },
    onBack () {
      this.$router.replace({ name: 'AdminDashboard' })
    }
  }
}
</script>

<style>
.table {
  width: 96%;
  margin: 0 auto;
}
.emsimg{
  height: 1000px;
  background-image: url(../assets/ems23.jpg);
}
</style>
