<template>
  <b-container>
    <div class="row justify-content-center">
      <b-div class="col-md-8">
        <b-card>
          <b-card-title>Admin Login</b-card-title>
          <b-card-body>
            <div v-if="error" class="alert alert-danger">{{error}}</div>
            <b-form action="#" @submit.prevent="submit">
              <b-row>
                <label for="email" class="col-md-4 col-form-label text-md-right">Email</label>

                <b-col>
                  <input
                    id="email"
                    type="email"
                    class="form-control"
                    name="email"
                    value
                    required
                    autofocus
                    v-model="form.email"
                  />
                </b-col>
              </b-row>
              <br>
              <b-row>
                <label for="password" class="col-md-4 col-form-label text-md-right">EmployeeId</label>

                <b-col>
                  <input
                    id="password"
                    type="password"
                    class="form-control"
                    name="password"
                    required
                    v-model="form.password"
                  />
                </b-col>
              </b-row>
              <br>
              <div class="form-group row mb-0">
                <div class="col-md-8 offset-md-4">
                  <button type="submit" class="btn btn-primary">Login</button>
                </div>
              </div>
            </b-form>
          </b-card-body>
        </b-card>
      </b-div>
    </div>
  </b-container>
</template>

<script>
import firebase from 'firebase'

export default {
  data () {
    return {
      form: {
        email: '',
        password: ''
      },
      error: null
    }
  },
  methods: {
    submit () {
      firebase
        .auth()
        .signInWithEmailAndPassword(this.form.email, this.form.password)
        .then(data => {
          this.$router.replace({ name: 'AdminDashboard' })
        })
        .catch(err => {
          this.error = err.message
        })
    }
  }
}
</script>

<style scoped>
  .card{
    background-image: url(../assets/ems3.jpg);
    width: 100%;
    height: 100%;
  }
</style>
