<template>
<div class="emsimg">
       <v-btn fab dark color="#002b80" @click="onBack">
         <v-icon color="white">mdi-arrow-left</v-icon>
      </v-btn>
<v-card
    class="mx-auto back"
    height=70rem,
    width=50rem
>
      <v-snackbar
      v-model="showAddEmployee"
      :timeout="timeout"
      color="#006600"
    >
      {{ successmsg }}</v-snackbar>
<v-card-title>Apply Leave</v-card-title>
  <form>
    <v-text-field
      v-model="applyleave.eid"
      label="EmployeeId"
      required
    ></v-text-field>
    <v-text-field
      v-model="applyleave.name"
      label="Name"
      required
    ></v-text-field>
    <v-text-field
      v-model="applyleave.sdate"
      label="Start Date"
      type="date"
      required
    ></v-text-field>
     <v-text-field
      v-model="applyleave.edate"
      label="End Date"
      type="date"
      required
    ></v-text-field>
    <br>
    <v-btn class="mx-4" color="#6699ff" @click="onSubmit">submit</v-btn>
  </form>
  </v-card>
    </div>
</template>

<script>

import firebase from 'firebase'
import router from '../router'
// import { validationMixin } from 'vuelidate'
// import { required, email } from 'vuelidate/lib/validators'

export default {
  data () {
    return {
      ref: firebase.firestore().collection('applyLeave'),
      applyleave: {},
      showAddEmployee: false,
      successmsg: 'Applied Leave',
      timeout: 3000
    }
  },

  computed: {
  },

  methods: {
    onSubmit (evt) {
      evt.preventDefault()

      this.ref.add(this.applyleave).then((docRef) => {
        this.applyleave.eid = ''
        this.applyleave.name = ''
        this.applyleave.sdate = ''
        this.applyleave.edate = ''
        router.push({
          name: 'ApplyLeave'
        })
      })
        .catch((error) => {
          alert('Error adding document: ', error)
        })
        .then(() => {
          this.showAddEmployee = true
        })
    },
    onBack () {
      this.$router.replace({ name: 'EmployeeDashboard' })
    }
  }
}
</script>

<style scoped>
.back{
    background-image: url(../assets/ems3.jpg);
    margin-top: 2rem;
    margin-bottom: 2rem;
}
form{
  margin-left: 5%;
  margin-right: 5%;
}
