<template>
<div class="emsimg">
<v-btn fab dark color="#002b80" @click="onBack">
<v-icon color="white">mdi-arrow-left</v-icon>
</v-btn>
<v-card
    class="mx-auto back"
    height=70rem,
    width=50rem
>
      <v-snackbar
      v-model="showAddEmployee"
      :timeout="timeout"
      color="#006600"
    >
      {{ successmsg }}</v-snackbar>
<v-card-title>Assign Project</v-card-title>
  <form>
    <v-text-field
      v-model="assp.eid"
      type="text"
      label="EmployeeId"
      required
    ></v-text-field>
    <v-text-field
      v-model="assp.pname"
      type="text"
      label="Project Name"
      required
    ></v-text-field>
    <v-text-field
      v-model="assp.ddate"
      label="Date"
      type="date"
      required
    ></v-text-field>
    <br>
    <v-btn class="mx-4" color="#6699ff" @click="onSubmit">submit</v-btn>
  </form>
  </v-card>
  </div>
</template>

<script>
// import { validationMixin } from 'vuelidate'
// import { required, email } from 'vuelidate/lib/validators'

import firebase from 'firebase'
import router from '../router'

export default {
  data () {
    return {
      ref: firebase.firestore().collection('assProject'),
      assp: {},
      showAddEmployee: false,
      successmsg: 'Assign Project Successfully',
      timeout: 3000
    }
  },

  methods: {
    onSubmit (evt) {
      evt.preventDefault()

      this.ref.add(this.assp).then((docRef) => {
        this.assp.pname = ''
        this.assp.ddate = ''
        this.assp.eid = ''
        router.push({
          name: 'AssignProject'
        })
      })
        .catch((error) => {
          alert('Error adding document: ', error)
        })
        .then(() => {
          this.showAddEmployee = true
        })
    },
    onBack () {
      this.$router.replace({ name: 'AdminDashboard' })
    }
  }
}
</script>

<style scoped>
.back{
  background-image: url(../assets/ems3.jpg);
  margin-top: 2rem;
  margin-bottom: 2rem;
}
form{
  margin-left: 5%;
  margin-right: 5%;
}
.emsimg{
  height: 1000px;
  background-image: url(../assets/ems23.jpg);
}
</style>

