import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Demo from '../components/Demo.vue'
import AddEmployee from '../components/AddEmployee.vue'
import ViewEmployee from '../components/ViewEmployee.vue'
import AssignProject from '../components/AssignProject.vue'
import ProjectStatus from '../components/ProjectStatus.vue'
import ProjectStatusEmp from '../components/ProjectStatusEmp.vue'
import EmployeeLeave from '../components/EmployeeLeave.vue'
import SalaryTable from '../components/SalaryTable.vue'
import ApplyLeave from '../components/ApplyLeave.vue'
import AdminLogin from '../components/AdminLogin.vue'
import EmployeeLogin from '../components/EmployeeLogin.vue'
import AdminDashboard from '../components/AdminDashboard.vue'
import EmployeeDashboard from '../components/EmployeeDashboard.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/Demo',
    name: 'Demo',
    component: Demo
  },
  {
    path: '/AddEmployee',
    name: 'AddEmployee',
    component: AddEmployee
  },
  {
    path: '/AssignProject',
    name: 'AssignProject',
    component: AssignProject
  },
  {
    path: '/ViewEmployee',
    name: 'ViewEmployee',
    component: ViewEmployee
  },
  {
    path: '/ProjectStatus',
    name: 'ProjectStatus',
    component: ProjectStatus
  },
  {
    path: '/ProjectStatusEmp',
    name: 'ProjectStatusEmp',
    component: ProjectStatusEmp
  },
  {
    path: '/EmployeeLeave',
    name: 'EmployeeLeave',
    component: EmployeeLeave
  },
  {
    path: '/SalaryTable',
    name: 'SalaryTable',
    component: SalaryTable
  },
  {
    path: '/ApplyLeave',
    name: 'ApplyLeave',
    component: ApplyLeave
  },
  {
    path: '/AdminLogin',
    name: 'AdminLogin',
    component: AdminLogin
  },
  {
    path: '/EmployeeLogin',
    name: 'EmployeeLogin',
    component: EmployeeLogin
  },
  {
    path: '/AdminDashboard',
    name: 'AdminDashboard',
    component: AdminDashboard
  },
  {
    path: '/EmployeeDashboard',
    name: 'EmployeeDashboard',
    component: EmployeeDashboard
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
