      </v-btn>
<v-card
    class="mx-auto back"
    height=70rem,
    width=50rem
>
      <v-snackbar
      v-model="showAddEmployee"
      :timeout="timeout"
      color="#006600"
    >
      {{ successmsg }}</v-snackbar>
 <v-card-title>Employee Resistration</v-card-title>
  <form>
    <v-text-field
      v-model="regi.fname"
      label="Name"
      type="text"
      required
    ></v-text-field>
    <v-text-field
