      label="E-mail"
      type="email"
      required
    ></v-text-field>
    <v-text-field
      v-model="regi.date"
      label="Birthdate"
      type="date"
      required
    ></v-text-field>
    <v-text-field
      v-model="regi.number"
      label="Contact Number"
      type="text"
      required
    ></v-text-field>
    <v-text-field
      v-model="regi.address"
      label="Address"
      type="text"
      required
    ></v-text-field>
    <v-text-field
      v-model="regi.deprtment"
      label="Department"
      type="text"
      required
    ></v-text-field>
    <v-text-field
      v-model="regi.degree"
      label="Degree"
      type="text"
      required
    ></v-text-field>
    <v-text-field
      v-model="regi.amt"
      label="Salary"
      type="number"
      required
    ></v-text-field>
    <v-btn class="mx-4" color="#6699ff" @click="onSubmit">submit</v-btn>
  </form>
  </v-card>
   </div>
</template>

<script>

import firebase from 'firebase'
import router from '../router'

export default {
  data () {
    return {
      ref: firebase.firestore().collection('emsDemo'),
      regi: {},
      showAddEmployee: false,
      successmsg: 'Successfully Register Employee',
      timeout: 3000
    }
  },

  computed: {

  },
  methods: {
    onSubmit (evt) {
      evt.preventDefault()

      this.ref.add(this.regi).then((docRef) => {
        this.regi.fname = ''
        this.regi.email = ''
        this.regi.date = ''
        this.regi.number = ''
        this.regi.address = ''
        this.regi.degree = ''
        this.regi.amt = ''
        this.regi.eid = ''
        this.regi.deprtment = ''
        router.push({
          name: 'AddEmployee'
        })
      })
        .catch((error) => {
          alert('Error adding document: ', error)
        })
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.regi.email, this.regi.eid)
        .then(data => {
          data.user
            .updateProfile({
              displayName: this.regi.fname
            })
            .then(() => {
              this.showAddEmployee = true
            })
        })
        .catch(err => {
          this.error = err.message
        })
    },
    onBack () {
      this.$router.replace({ name: 'AdminDashboard' })
    }
  }
}
</script>

