<template>
<b-container fluid class="emsimg">
       <v-btn fab dark color="#002b80" @click="onBack">
         <v-icon color="white">mdi-arrow-left</v-icon>
      </v-btn>
  <b-row>
    <b-col cols="12">
      <b-table striped hover :items="assProject" :fields="fields">
        <template slot="actions" scope="row">
          <b-btn size="sm" @click.stop="details(row.item)">Details</b-btn>
        </template>
      </b-table>
    </b-col>
  </b-row>
  </b-container>
</template>

<script>

import firebase from 'firebase'
import router from '../router'

export default {
  name: 'BoardList',
  data () {
    return {
      fields: {
        employeeId: { label: 'employeeId', sortable: true, class: 'text-left' },
        projectName: { label: 'projectName', sortable: true, class: 'text-left' },
        dueDate: { label: 'dueDate', sortable: true, class: 'text-left' }
        // actions: { label: 'Action', class: 'text-center' }
      },
      assProject: [],
      errors: [],
      ref: firebase.firestore().collection('assProject')
    }
  },
  created () {
    this.ref.onSnapshot((querySnapshot) => {
      this.assProject = []
      querySnapshot.forEach((doc) => {
        this.assProject.push({
        //   key: doc.id,
          employeeId: doc.data().eid,
          projectName: doc.data().pname,
          dueDate: doc.data().ddate
        })
      })
    })
  },
  methods: {
    details (board) {
      router.push({ name: 'ShowBoard', params: { id: board.key } })
    },
    onBack () {
      this.$router.replace({ name: 'EmployeeDashboard' })
    }
  }
}
</script>

